import { getAlbumDetail } from '@/api/homeApi';
import goDetail from '@/components/goDetail';
export default {
	components: {
		goDetail
	},
	data() {
		return {
			params: {
				limit: 30,
				order: 'new',
				skip: 0,
				first: 1
			},
			id: -1,
			wallpaper: [],
			album: {},
			hasMore: true
		};
	},
	onLoad({ id }) {
		this.id = id;
		this.getAlbumData(id);
	},
	methods: {
		getAlbumData(id) {
			console.log(id);
			getAlbumDetail({ ...this.params, id }).then(res => {
				let data = res.data.res;
				console.log(data);
				if (Object.keys(this.album).length === 0) {
					this.album = data.album;
					console.log(this.album.name);
					uni.setNavigationBarTitle({
						title: this.album.name
					});
				}
				if (data.wallpaper.length === 0) {
					this.hasMore = false;
					uni.showToast({
						title: '没有更多数据了',
						icon: none
					});
					return;
				}
				this.wallpaper = [...this.wallpaper, ...data.wallpaper];
			});
		}
	},
	// 页面触底 上拉加载下一页事件
	onReachBottom() {
		// console.log("我也是有底线的");
		if (this.hasMore) {
			this.params.first = 0;
			this.params.skip += this.params.limit;
			this.getAlbumData(this.id);
		} else {
			uni.showToast({
				title: '没有更多数据了',
				icon: 'none'
			});
		}
	}
};
